import { apiHelper } from '.././src/utility/helpers'

export default {
  deleteMessages(){
    return apiHelper.delete('/api/messages/all')
  },
  getNextPage({ page, roomName}){
    return apiHelper.post('/api/messages/getNextPage', { page, roomName })
  },
  uploadImg({formData}) {
    return apiHelper.post('/api/messages/uploadImg', formData)
  }
}